
































































































import { Vue, Component, Watch } from "vue-property-decorator";
import AddPersonProp from "./components/AddPersonProp.vue";

import api from "../../api";
import { formatterAttrType } from "@/utils/dataFormat";

@Component({
  // 过滤器
  filters: {
    formatterAttrType
  },
  components: {
    AddPersonProp
  }
})
export default class PersonCustomProps extends Vue {
  // 类型树信息
  treeMessage: any = {
    // 过滤条件
    filterText: "",
    // tree数据
    data: [],
    // 配置选项
    defaultProps: {
      children: "children",
      label: "name"
    }
  };
  // 选中表单信息
  basicForm: any = {};

  // 添加或编辑树节点弹窗
  dialogTitle = "";
  visibleDialog: boolean = false;
  // 弹窗表单信息
  formData: any = {
    id: "",
    name: "",
    code: ""
  };

  hasCustomRole: boolean = qTouch.getBaseTypeByCode("person");

  created() {
    this.getTypeTreeData();
  }

  // 根据父组件参数获取类型树数据
  getTypeTreeData() {
    let _this = this;
    this.$http.get(api.getTypeDetail + "/findTypeTree/person").then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          let data = res.data.data;
          _this.treeMessage.data = data ? data : [];
          if (!this.basicForm.id) {
            this.handleNodeClick(this.treeMessage.data[0]);
          }
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = Object.assign({}, data);
    if (this.basicForm.icon)
      this.basicForm.icons = [
        {
          name: "icon",
          url: this.basicForm.icon
        }
      ];
  }

  // 监听是否tree过滤条件
  @Watch("treeMessage.filterText")
  setFilterText(val: any) {
    (this.$refs.tree as any).filter(val);
  }

  // 过滤tree
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.treeMessage.defaultProps.label].indexOf(value) !== -1;
  }

  // 打开节点弹窗
  openTypeTreeNodeForm(buttonName: any, data: any) {
    switch (buttonName) {
      case "add":
        this.dialogTitle = "添加人员类型";
        this.formData = {
          id: "",
          name: "",
          code: ""
        };
        this.formData.parentId = data.id;
        this.formData.level = data.level + 1;
        break;
      case "edit":
        this.dialogTitle = "编辑人员类型";
        this.formData = Object.assign({}, data);
        break;
      case "addTop":
        this.dialogTitle = "添加人员类型";
        this.formData = {
          id: "",
          name: "",
          code: ""
        };
        this.formData.level = 1;
        break;
    }
    this.visibleDialog = true;
  }

  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(api.deleteType + "/" + data.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message.success("删除成功");
              this.getTypeTreeData();
            } else {
              this.$message.error(res.data.message);
            }
          }
        });
      })
      .catch(() => {});
  }

  // 保存树节点
  saveTypeAndAttr(data: any) {
    this.visibleDialog = false;
    (this.$refs["baseDataInfoTable"] as any).getCustomAttributeList();
    this.getTypeTreeData();
    this.basicForm = Object.assign({}, data);
  }

  closeDialog() {
    this.visibleDialog = false;
  }
}
